<template>
	<div>
		<div style="padding: 10px;" @click="share">分享</div>
		<div style="padding: 10px;" @click="recharge">充值</div>
		<div style="padding: 10px;" @click="phone">拍照</div>
		<div style="padding: 10px;" @click="login">login</div>
		
		<div style="padding: 10px;" @click="goindex">goindex</div>
		<div style="padding: 10px;" @click="getdata">getData</div>
	</div>
</template>

<script>
export default {
    created() {
    	
    	
    	
    	mqq.invoke('ui','setOnShareHandler',function(type){
		    mqq.invoke('ui','shareMessage',{
		        title: '鄢康杰快来',
		        desc: '好好玩的',
		        share_type: type,
		        share_url: window.OPEN_DATA.shareurl,
		        image_url: 'https://yingdd.oss-cn-hangzhou.aliyuncs.com/0d25d14de142d89f5af33b0b53ecc7b0.png',
		        back: true
		    },function(result){
		        //result
		    });
		});
    },
    methods: {
    	getdata() {
    		alert(mqq)
    		alert(JSON.stringify(window.OPEN_DATA))
    	},
    	goindex() {
    		this.$router.push('/index')
    	},
    	login() {
    		this.$api.wanbaLogin({
	    		platform: window.OPEN_DATA.platform,
	    		openid: window.OPEN_DATA.openid,
	    		openkey: window.OPEN_DATA.openkey
	    	}).then(res => {
		      alert(JSON.stringify(res))
		    }, err => {
		        
		    })
//	    	this.$api.wanbaLogin({
//	    		platform: 11,
//	    		openid: 11,
//	    		openkey: 11
//	    	}).then(res => {
//		      alert(JSON.stringify(res))
//		    }, err => {
//		        
//		    })
//	    	this.$api.homeBanner().then(res => {
//				alert(res.data)
//		    }, err => {
//		    	  
//			})
    	},
    	share() {
    		mqq.ui.showShareMenu()
    	},
    	phone() {
    		mqq.invoke("media", "getPicture",{
			     source:1,
			     front:false,
			     max:1,
			     outMaxWidth:1600,
			     outMaxHeight:1600,
			     inMinWidth:70,
			     inMinHeight:70,
			     urlOnly:false
			},function(ret,data){
			    alert(JSON.stringify(data));
			});
    	},
    	recharge() {
    		window.popPayTips({
			    defaultScore : 5,
			    appid : 1106587346
			})
    		window.__paySuccess = function(){
			    alert('succ')
			}
			
			window.__payError = function(){
			    alert('fail')
			}
			
			window.__payClose = function(){
			    alert('close')
			}
    	}
    }
}
</script>





